body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
    width: 100%;
    height: 100vh;
}

.container--home-page {
    height: 100%;
    background-color: #eee;
}

.container--search-bar {
    padding: 0 10px;
    margin-bottom: 10px;
}

.container--header {
	width: 100%;
    position: relative;
    background-color: #eee;
}

.container--dropdown {
    display: -webkit-flex;
    display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
    padding: 2px;
}

.dropdown-content {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.dropdown-text {
    font-size: 15px;
    margin: 0 5px;
    text-transform: none;
    font-weight: 700;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 200px;
    white-space: nowrap;
}

.dropdown-icon {
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsMAAA7DAcdvqGQAAABISURBVEhL7c0xDgAgCENRju7NVYYmDMYAYlj6EkZ+hYh6jH0zefrrkhlxxyEyEo6DZyQdh9vIcxxOI2VxsCPlcdDwtzhRC5EFRZc/yYhthkIAAAAASUVORK5CYII=);
    height: 15px;
    width: 15px;
    background-size: 15px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}

.dropdown-icon-rotate {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.container--more-options {
    top: 0;
    right: 0;
    position: absolute;
}

.more-options {
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsMAAA7DAcdvqGQAAABESURBVEhLYxgF1ATeQPwEir1AAtQGj4H4PxQ/AglQG9DcAlCwgCwBGe4OEhgFdAejyZQgGE2mAw9GkylBMJpMaQEYGAChtySsyddFaAAAAABJRU5ErkJggg==);
    height: 18px;
    width: 18px;
    background-size: 18px;
}

.container--list-item {
    border-bottom: 1px solid #eee;
    background: #fff;
    z-index: 10;
    position: fixed;
    width: 100%;
    border-radius: 0 0 5px 5px;
    box-shadow: 0px 1px 4px #b3b3b3;
    opacity: 1;
    -webkit-transition: opacity .5s;
    transition: opacity .5s;

    max-height: 65vh;
    overflow: auto;
}

.list-item {
    height: 42px;
    line-height: 42px;
    text-align: center;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    padding: 0 5%;
}

.list-item-label__bold {
    font-weight: 700;
}

.list-item-label {
    max-width: 250px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list-item:last-child {
    -webkit-justify-content: center;
            justify-content: center;
}

.plus-icon {
    margin: 0 5px;
    color: #22cccc;
    font-size: 20px;
}

.separator {
    width: 90%;
    margin: 0 auto;
    opacity: 0.5;
    border-bottom: 1px solid #bdbdbd;
}

.new-folder-container {
    display: -webkit-flex;
    display: flex;
}

.container--notes {
    padding: 0 10px 10px 10px;
    height: calc(100vh - 92px);
    overflow: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
}

.container--add-notes {
    position: fixed;
    bottom: 30px;
    right: 30px;
}

.container--selectionn-header {
    display: -webkit-flex;
    display: flex;
    height: 42px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.container--bottom-toolbar {
    position: fixed;
    bottom: 0;
    height: 56px;
    width: 100%;
    background-color: #f9f9f9;
    display: -webkit-flex;
    display: flex;
    -webkit-transition: height .5s;
    transition: height .5s;
    border-radius: 5px 5px 0 0;
}

.container--bottom-tool-button {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 2;
}

.bottom-button-text {
    font-size: 14px;
    text-transform: none;
    font-weight: 500;
    margin: 0 10px;
}

.time-stamp-wrappper {
    position: absolute;
    bottom: 10px;
    left: 5px;
}
.container--note-page {
    height: 100%;
    padding: 10px;
    box-sizing: border-box
}

.container--header-toolbar {
    display: -webkit-flex;
    display: flex;   
}

.container--circle-icon {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 25px;
    width: 25px;
    margin-left: auto;
}

.container--note-body{
    height: calc(100% - 25px);
    overflow: hidden;
    padding: 5px;
}

blockquote {
    border-left: 2px solid #ddd;
    margin-left: 0;
    margin-right: 0;
    padding-left: 10px;
    font-style: italic;
}

.text-view-content {
    font-size: 14px;
    color: #5f6368;
    margin: 5px;
}

.text-view-content--separator {
    font-size: 12px;
    line-height: 14px;
    vertical-align: middle;
    color: #5f6368;
}

.container--info {
    margin: 5px 0 0 0;
}
